
import React, {useContext} from 'react';
import {PokeListContext} from '../contexts/PokelistContext';
import {TeamContext} from '../contexts/TeamContext'
import './SearchBar.scss'

const SearchBar=()=>{
    const {search, setSearch, pokes, gameStart }=useContext(PokeListContext);
    const {team, setTeamA}=useContext(TeamContext);

    if(search.length>0 && pokes.length<1) {
        const msg=document.querySelector("nav .msg");
        msg.innerText="no this pokemon..."
        setTimeout(() => {
        setSearch('');    
            msg.innerText=""
        }, 1000);
    } 

    const submitTeam=(e)=>{
        setSearch(e.target.value);
        setTeamA(team)
    }
    
    return(
        <div>
            {!gameStart  && 
                <div className='SearchBar'>
                    <input id='auto' type='text' 
                        value={search}
                        onChange={(e)=>{submitTeam(e)}}
                        required
                    />  
                </div>
            }
            <p className="msg"></p>
        </div>
    )
}

export default SearchBar;