import React, { useContext } from 'react';
import {
    BrowserRouter as Router,
    Route,
    Switch, 
  } from 'react-router-dom'

import Navbar from './Navbar';
import Home from './Home';
import PokePlay from './PokePlay';
import ScoreSheet from './ScoreSheet';
import ScoreDetail from './ScoreDetail';
import BG from './BG';

import { PokeListContext } from '../contexts/PokelistContext';

function GameApp(props) {
    const { loading } = useContext(PokeListContext);

    if(loading) return <div className='loader-wrapper'><div className='loader'></div></div>;

    function openFullscreen() {
      // if(window.screen.availWidth>1100){
        // const elem = document.querySelector(".GameApp"); 
       const elem = document.documentElement; 
       if (elem.requestFullscreen) {
          elem.requestFullscreen();
        } else if (elem.mozRequestFullScreen) { /* Firefox */
          elem.mozRequestFullScreen();
        } else if (elem.webkitRequestFullscreen) { /* Chrome, Safari and Opera */
          elem.webkitRequestFullscreen();
        } else if (elem.msRequestFullscreen) { /* IE/Edge */
          elem.msRequestFullscreen();
        }    
      // } else {
      //   console.log('screen size under 900px')
      // }
  }

    function toggleFullScreen(event){
        if (document.fullscreenElement || document.webkitFullscreenElement || document.mozFullscreenElement || document.msFullscreenElement) { 
          document.exitFullscreen()
            .then(() => console.log("Document Exited form Full screen mode"))
            .catch((err) => console.error(err))
        } else { 
          document.documentElement.requestFullscreen().catch(console.log)
        } 
    }

    return(
        <div className="GameApp">
          <Router>
            <BG openFullscreen={openFullscreen}/>
            <Navbar toggleFullScreen={toggleFullScreen}/>
              <Switch>
                  <Route exact path='/pokegame' component={routeProps=><Home stuff={routeProps} openFullscreen={openFullscreen}/>} />
                  <Route exact path='/pokegame/pokeplay' render={()=><PokePlay/>}/>
                  <Route exact path='/pokegame/scoresheet' component={ScoreSheet} />                
                  <Route exact path='/pokegame/scoresheet/detail/:round' component={ScoreDetail} />   
                  <Route component={routeProps=><Home stuff={routeProps} openFullscreen={openFullscreen}/>} />
              </Switch>   
        </Router> 
      </div>
    )
}

export default GameApp;