import React from 'react';
import {
    NavLink,
    withRouter
} from "react-router-dom";

import SearchBar from './SearchBar';
import './Navbar.scss';

function Navbar (props){

      var userAgent = navigator.userAgent || navigator.vendor || window.opera;
    return (
        <>
        { props.location.pathname.length>11 &&
            <nav id='nav'>
                {props.location.pathname.indexOf('pokeplay')!==-1 ? <SearchBar/> : <div></div>}
                <ul ref={props.fullscreenModal}>
                    {(window.screen.availWidth>900 && (/windows phone/i.test(userAgent)!==true && /android/i.test(userAgent)!==true && (/iPad|iPhone|iPod/.test(userAgent) && !window.MSStream)!==true)) &&
                        <button className='btn btn-animate tooltip fullscreenBtn' onClick={props.toggleFullScreen} >
                                <p>[]</p>
                            <span className="tooltiptext">full screen</span>
                        </button>                                       
                    }
                    <NavLink exact className='btn btn-white btn-animate tooltip' activeClassName='active-link' to='/pokegame/' data-lan='tab'>
                        <i className="fas fa-home"></i><span className="tooltiptext">home</span>
                        </NavLink>
                    <NavLink exact className='btn btn-white btn-animate tooltip' activeClassName='active-link' to='/pokegame/pokeplay' data-lan='tab'>
                        <i className="fas fa-play"></i><span className="tooltiptext">play game</span>
                        </NavLink>
                    <NavLink exact className='btn btn-white btn-animate tooltip scorelink' activeClassName='active-link' to='/pokegame/scoresheet' data-lan='tab'>
                        <i className="fas fa-clipboard"></i><span className="tooltiptext">score sheet</span>
                        </NavLink>
                </ul>  
            </nav>         
        }
        </>
    )
}
  
export default withRouter(Navbar);